import * as BDTLL from "../BDTLL";
import * as Sentry from "@sentry/browser";

let browser = require("webextension-polyfill");

const nativeApplicationName: string = 'com.extension.av.communication';

export interface ISession {
    tabs: {[tabId: string]: WebPage};
    scanURL(url: string, tabId: string): Promise<WebPage>;
    scanThreat(request: any, tabId: string): Promise<WebPage>;
    checkBlockedPage(tabId: string, page: WebPage): Promise<boolean>;
}

export class WebPage {
    url: string;
    threatStatus: BDTLL.PageStatus;
    scanned = false;
    sessionWhitelisted = false;
    trackerList: Array<string>;
    timestamp: number;

    constructor(url: string, threatStatus: BDTLL.PageStatus = null) {
        this.url = url;
        this.threatStatus = threatStatus;
        this.timestamp = Date.now();
    }
}

export class Session implements ISession, BDTLL.ISwitchable {
    storage: BDTLL.IStorage;
    appWhitelist: BDTLL.IWhitelist;
    scanner: BDTLL.IScanner;
    defaultPageStatus: BDTLL.PageStatus;
    currentBrowser: string;

    whitelistHandler = {
        set: (obj: {[url: string]: WebPage}, url: string, newPage: WebPage, receiver: any) => {
            obj[url] = newPage;
            obj[url].sessionWhitelisted = true;

            this.storage.set(BDTLL.StorageKeys.SESSION_WHITELIST, obj).then(() => {
                // Then nothing, the value was susccesfully set
            }).catch((error: Error) => {
                if (BDTLL.DEBUG_MODE) {
                    console.error(`Cannot set ${BDTLL.StorageKeys.SESSION_WHITELIST}: ${error}`);
                }
            });

            return true;
        },
        get: (obj: {[url: string]: WebPage}, url: string, receiver: any) => {
            if (obj[url] == null)
                this.whitelist[url] = this.history[url];

            return obj[url];
        },
        has: (obj: {[url: string]: WebPage}, urlToTest: string) => {
            let domain = BDTLL.Utils.extractRootDomain(urlToTest);

            for (let url in obj)
                if (domain == BDTLL.Utils.extractRootDomain(url))
                    return true;

            return false;
        }
    };

    whitelist = new Proxy<{[url: string]: WebPage}>({}, this.whitelistHandler);

    tabs = new Proxy<{[tabId: string]: WebPage}>({}, {
        set: (obj: {[tabId: string]: WebPage}, tabId: string, newPage: WebPage, receiver: any) => {
            const page = BDTLL.Utils.safelyParseJSON(newPage);
            obj[tabId] = page;

            this.setBrowserAction(tabId, page);

            return true;
        }
    });

    HISTORY_MAX_LENGTH = 20;
    history = new Proxy<{[url: string]: WebPage}>({}, {
            set: (obj: {[url: string]: WebPage}, url: string, newPage: WebPage, receiver: any) => {
                var count = 0, oldestTimestamp = Date.now(), oldestKey = "";

                for (let key in obj) {
                    if (obj[key].timestamp < oldestTimestamp)
                        oldestKey = key;
                    count++;
                }

                if (count > this.HISTORY_MAX_LENGTH)
                    delete obj[oldestKey];

                // obj[url] = JSON.parse(JSON.stringify(newPage));
                obj[url] = BDTLL.Utils.safelyParseJSON(newPage);
                obj[url].timestamp = Date.now();
                return true;
            },
            get: (obj: {[url: string]: WebPage}, url: string, receiver: any) => {
                var page = obj[url];

                if (page == null) {
                    page = new WebPage(url);
                    obj[url] = page;
                } else {
                    page.timestamp = Date.now();
                }

                return page;
            }
        });

    processPage: {[key: string]: {processor: (request: any) => WebPage, active: boolean}} = {
        [BDTLL.SettingType.THREAT_FILTER]: {
            processor: this.scanThreat.bind(this),
            active: false
        }
    };

    constructor(storage: BDTLL.IStorage, appWhitelist: BDTLL.IWhitelist, scanner: BDTLL.IScanner) {
        this.storage = storage;
        this.appWhitelist = appWhitelist;
        this.scanner = scanner;
        this.currentBrowser = BDTLL.Utils.getCurrentBrowser();

        storage.get(BDTLL.StorageKeys.SESSION_WHITELIST).then((sessionWhitelist: any) => {
            if (typeof(sessionWhitelist) !== "undefined") {
                this.whitelist = new Proxy<{[url: string]: WebPage}>(sessionWhitelist, this.whitelistHandler);
            } else {
                this.whitelist = new Proxy<{[url: string]: WebPage}>({}, this.whitelistHandler);
            }
        }).catch((error: Error) => {
            this.whitelist = new Proxy<{[url: string]: WebPage}>({}, this.whitelistHandler);

            if (BDTLL.DEBUG_MODE) {
                console.error(`Cannot get ${BDTLL.StorageKeys.SESSION_WHITELIST}: ${error}`);
            }
        });

        browser.alarms.get("tll-sp-timer").then((alarm: any) => {
			if (typeof(alarm) === "undefined") {
				browser.alarms.create(
                    "tll-sp-timer",
                    {
                        delayInMinutes: BDTLL.REPORT_SCANNED_PAGES_TIME_INTERVAL,
                        periodInMinutes: BDTLL.REPORT_SCANNED_PAGES_TIME_INTERVAL,
                    }
                );
			} else {
				if (BDTLL.DEBUG_MODE) {
					console.log("tll-update-timer already registered!");
				}
			}
		});

        this.listen();
    }

    async sessionWhitelist(tabId: string) {
        // At this point, the URL of the object in tabs[tabId] = our blocked page
        const parsedURL: URL = new URL(this.tabs[tabId].url);
        let url: string = parsedURL.searchParams.get("url") || this.tabs[tabId].url;

        this.tabs[tabId].url = url;
        this.whitelist[url] = this.tabs[tabId];

        // if (BDTLL.DEBUG_MODE) {
        //     console.log(`sessionWhitelist(): this.whitelist[${url}] = ${JSON.stringify(this.tabs[tabId], null, 4)}`);
        // }

        return true;
    }

    async handlePageScan(request: any, tabId: string) {
        try {
            var page: WebPage;

            for (let setting in this.processPage)
                if (this.processPage[setting].active)
                    page = await this.processPage[setting].processor(request);

            this.tabs[tabId] = page;

            if (request.url in this.whitelist)
                return page;

            const isBlocked = await this.checkBlockedPage(tabId, page);

            return page;
        } catch(err) { }
    }

    async scanURL(url: string) {
        try {
            var page = this.history[url]; // This can be the default value when a WebPage is created

            if (this.appWhitelist.isWhitelisted(url)) {
                // page = JSON.parse(JSON.stringify(page))
                page = BDTLL.Utils.safelyParseJSON(page);
                page.threatStatus = BDTLL.PageStatus.WHITELISTED
                return page;
            }

            if (url in this.whitelist) {
                page = this.whitelist[url];
            }

            if (page.threatStatus == null) {
                page.threatStatus = await this.scanner.scanLink(url);
            }

            return page;
        } catch(err) { }
    }

    async scanThreat(request: any) {
        try {
            var page = this.history[request.url]; // This can be the default value when a WebPage is created

            if (this.appWhitelist.isWhitelisted(request.url)) {
                // page = JSON.parse(JSON.stringify(page))
                page = BDTLL.Utils.safelyParseJSON(page);
                page.threatStatus = BDTLL.PageStatus.WHITELISTED
                return page;
            }

            if (request.url in this.whitelist) {
                page = this.whitelist[request.url];
            }

            if (!page.scanned) {
                var verdict = await this.scanner.scanPage(request);
                page.threatStatus = (verdict == BDTLL.PageStatus.PHISHING) ? verdict: page.threatStatus
                page.scanned = true;
            }

            if (page.threatStatus === null) {
                page = await this.scanURL(page.url);
            }

            return page;
        } catch(err) { }
    }

    async getPageInfo(): Promise<WebPage> {
        try {
            const currentTab = await BDTLL.Utils.getCurrentTab();
            const tabId = currentTab.id;

            if (!(tabId in this.tabs)) {
                // In case the service worker becomes inactive, tabs object will be empty
                // thus, tabId will not be found. And in case the user clicks on the
                // action button, in order to get the information to be displayed on the
                // popup, we need to scan the URL first.
                this.tabs[tabId] = await this.scanURL(currentTab.url);
            }

            let page = this.tabs[tabId];
            if (!this.processPage[BDTLL.SettingType.THREAT_FILTER].active) {
                page.threatStatus = BDTLL.PageStatus.DISABLED;
            }

            return page;
        } catch(err) { }
    }

    async checkBlockedPage(tabId: string, page: WebPage): Promise<boolean> {
        if (!BDTLL.Utils.isMaliciousPage(page)) {
            return false;
        }

        try {
            const tab = await browser.tabs.update(tabId, {
                url: `/pages/blocked/blocked.html?status=${page.threatStatus}&url=${encodeURIComponent(page.url)}`
            });

            this.sendBlockedPages([page]);
            return true;
        } catch (error: any) {
            // Might happen with an invalid tabID.
            return false;
        }
    }

    setBrowserAction(tabId: string, page: WebPage) {
        try {
            let threatStatus = this.defaultPageStatus;
            if (typeof page !== 'undefined') {
                threatStatus =
                    this.processPage[BDTLL.SettingType.THREAT_FILTER].active ?
                    page.threatStatus :
                    this.defaultPageStatus;
            }

            // console.log(`setBrowserAction: ${JSON.stringify(page, null, 4)}`);

            if (typeof tabId !== 'undefined') {
                // setIcon is not supported for android devices
                // https://developer.mozilla.org/en-US/docs/Mozilla/Add-ons/WebExtensions/API/browserAction
                if (!navigator.userAgent.match(/Android/i)) {
                    browser.action.setIcon({
                        path: BDTLL.toBarIcon[threatStatus],
                        tabId: +tabId
                    }, () => {
                        if (browser.runtime.lastError) {
                            if (browser.runtime.lastError.message.includes("No tab with id")) {
                                // In case of this error we have nothing to do
                                if (BDTLL.DEBUG_MODE) {
                                    console.error(`setIcon error: No tab with id ${tabId}`);
                                }
                            } else {
                                // Otherwise, in case of other errors, send them in Sentry.
                                Sentry.captureException(new Error(browser.runtime.lastError.message));
                            }
                        }
                    });
                }
            }
        } catch(err) { }
    }

    sendBlockedPages(pages: [WebPage]) {
        const blockedPages = Array.from(pages);

        if (BDTLL.DEBUG_MODE) {
            console.log(`Blocked pages: ${JSON.stringify(blockedPages, null, 4)}`);
        }

        if (blockedPages.length > 0) {
            let message = {
                request: 'blockedPages',
                browser: this.currentBrowser,
                pagesBlocked: blockedPages
            };

            browser.runtime.sendNativeMessage(nativeApplicationName, message).then((response: any) => {
                if (BDTLL.DEBUG_MODE) {
                    console.log("Blocked pages sent succesfully!");
                    console.log(response.service_status);
                }
            }).catch((error: any) => {
                if (BDTLL.DEBUG_MODE) {
                    console.error(error);
                }
            });
        }
    }

    sendScannedPages() {
        const scannedPages: number = this.scanner.getNumberOfScannedPages();
        if (BDTLL.DEBUG_MODE) {
            console.log(`Sending scanned pages: ${scannedPages}`);
        }

        if (scannedPages > 0) {
            let message = {
                request: 'scannedPages',
                browser: this.currentBrowser,
                scannedPages: scannedPages
            }
            browser.runtime.sendNativeMessage(nativeApplicationName, message).then((response: any) => {
                if (BDTLL.DEBUG_MODE) {
                    console.log("Scanned pages sent succesfully!");
                    console.log(response.service_status);
                }

                this.scanner.resetScannedPagesCounter();
            }).catch((error: any) => {
                // Refresh counter even if there was an error when sending the message.
                // In case the host is not installed on the user's computer
                // the counter will not be sent succesfully and thus the counter
                // will increase continously.
                this.scanner.resetScannedPagesCounter();
                if (BDTLL.DEBUG_MODE) {
                    console.error(`Failed to send the scanned pages: ${error}!`);
                }
            });
        }
    }

    enable(setting: BDTLL.SettingType): void {
        switch(setting) {
            case BDTLL.SettingType.THREAT_FILTER:
                this.processPage[BDTLL.SettingType.THREAT_FILTER].active = true;
                this.defaultPageStatus = BDTLL.PageStatus.SAFE;
                break;
        }
    }

    disable(setting: BDTLL.SettingType): void {
        switch(setting) {
            case BDTLL.SettingType.THREAT_FILTER:
                this.processPage[BDTLL.SettingType.THREAT_FILTER].active = false;
                this.defaultPageStatus = BDTLL.PageStatus.DISABLED;
                break;
        }
    }

    listen() {
        browser.tabs.onUpdated.addListener(
            async (tabId: any, changeInfo: any, tab: any) => {
                if (changeInfo.status == "complete") {
                    const currentTab = await BDTLL.Utils.getCurrentTab();

                    if (currentTab !== undefined && 'url' in currentTab) {
                        const currentURL: string = currentTab.url;

                        if (currentURL === 'chrome://newtab/' || currentURL=== 'about:home' || currentURL === 'about:blank') {
                            this.tabs[tabId] = new WebPage(currentURL, this.defaultPageStatus);
                        } else if (currentURL.indexOf("/pages/blocked/blocked.html?status=phishing&url=") > 0) {
                            this.tabs[tabId] = new WebPage(currentURL, BDTLL.PageStatus.PHISHING);
                        } else if (currentURL.indexOf("/pages/blocked/blocked.html?status=malware&url=") > 0) {
                            this.tabs[tabId] = new WebPage(currentURL, BDTLL.PageStatus.MALWARE);
                        } else if (currentURL.indexOf("/pages/blocked/blocked.html?status=fraud&url=") > 0) {
                            this.tabs[tabId] = new WebPage(currentURL, BDTLL.PageStatus.FRAUD);
                        } else if (currentURL.indexOf("/pages/blocked/blocked.html?status=miner&url=") > 0) {
                            this.tabs[tabId] = new WebPage(currentURL, BDTLL.PageStatus.MINER);
                        } else if (currentURL.indexOf("/pages/blocked/blocked.html?status=pua&url=") > 0) {
                            this.tabs[tabId] = new WebPage(currentURL, BDTLL.PageStatus.PUA);
                        } else if (currentURL.indexOf("/pages/settings/settings.html") > 0) {
                            this.tabs[tabId] = new WebPage(currentURL, BDTLL.PageStatus.SAFE);
                        } else {
                            this.setBrowserAction(tabId, this.tabs[tabId]);
                        }
                    }
                }
            }
        );

        browser.tabs.onRemoved.addListener(
            (tabId: string, removeInfo: any) => {
                delete this.tabs[tabId];
            }
        );

        BDTLL.MessageService.addListener(
            (message: any, sender: any) => {
                switch(message.command) {
                    case BDTLL.Command.SCAN_PAGE:
                        return this.handlePageScan(message.request, sender.tab.id);
                    case BDTLL.Command.GET_PAGE_INFO:
                        return this.getPageInfo();
                    case BDTLL.Command.SESSION_WHITELIST:
                        return this.sessionWhitelist(sender.tab.id);
                }
            }
        );

        browser.alarms.onAlarm.addListener((alarm: any) => {
            if (alarm.name === "tll-sp-timer") {
                this.sendScannedPages();
            }
        });

        browser.runtime.onStartup.addListener(() => {
            // `onStartup` event is triggered when a new session is created
            this.storage.remove(BDTLL.StorageKeys.SESSION_WHITELIST).then(() => {
                this.whitelist = new Proxy<{[url: string]: WebPage}>({}, this.whitelistHandler);
            });
        });
    }
}