import * as BDTLL from "../BDTLL";

export class assl_common {
    static getPath(url: any) {
        if (url.indexOf('?') < 0) {
            var pathRegex = /(http|https):\/\/.*?\/(.*)/;
        } else {
            var pathRegex = /(http|https):\/\/.*?\/(.*)\?/;
        }

        var path = pathRegex.exec(url);

        if (typeof(path) != "undefined" && path && typeof(path[2]) != "undefined" && path[2]) {
            return path[2];
        }
    }

    static getParams(url: any) {
        var params;
        if (url.indexOf('?') > 0) {
            var paramRegex = /(http|https):\/\/.*\/.*\?(.*)/;

            params = paramRegex.exec(url);
            return params[2];
        }
        return null;
    }

    static runRule(rule: any, meta: any, body_raw: any, body_decoded: any, url: any, title: any, domain: any, scripts: any) {
        var hitScripts = new Array();
        if (typeof(rule) != "undefined") {
            var match = null;
            var urlRegex = /(http|https):\/\/(www\.)?(.*)/;

            if (rule.regex != "regex fail") {
                if (typeof(rule.regex) == "string") {
                    if (typeof(rule.param) != "undefined" && rule.param) {
                        try {
                            if (rule.param == "REGEX_INSENSITIVE") {
                                rule.regex = new RegExp(rule.regex, "i");
                            } else {
                                rule.regex = new RegExp(rule.regex);
                            }
                        } catch (err) {}
                    }
                }
                if (rule.part == "HTML::Body") {
                    if (rule.encoding != null) {
                        if (rule.encoding == "DECODED") {
                            match = rule.regex.exec(body_decoded);
                        } else {
                            match = rule.regex.exec(body_raw);
                        }
                    } else {
                        match = rule.regex.exec(body_raw);
                    }
                } else if (rule.part == "HTML::Title") {
                    match = rule.regex.exec(title);
                } else if (rule.part == "HTML::Url") {
                    match = rule.regex.exec(url);
                } else if (rule.part == "HTML::Meta") {
                    match = rule.regex.exec(meta);
                } else if (rule.part == "HTML::Script") {
                    for (var i = 0; i < scripts.length; i++) {
                        match = rule.regex.exec(scripts[i]);

                        if (match != null) {
                            hitScripts.push(scripts[i]);
                        }
                        match = null;
                    }
                    if (hitScripts.length > 0) {
                        match = ["true"];
                    }
                } else if (rule.part == "Url::Path") {
                    var path = BDTLL.assl_common.getPath(url);
                    if (typeof(path) != 'undefined' && path) {
                        match = rule.regex.exec(path);
                    }
                } else if (rule.part == "Url::Params") {
                    var params = BDTLL.assl_common.getParams(url);
                    if (typeof(params) != 'undefined' && params) {
                        match = rule.regex.exec(params);
                    }
                } else if (rule.part == "Url::Host") {
                    match = rule.regex.exec(domain);
                }
            }
            if (match != null) {
                return {
                    verdict: true,
                    scripts: hitScripts
                }
            }
        }
        return {
            verdict: false,
            scripts: hitScripts
        }
    }

    static extractRules(logicalExpression: any) {
        var items = new Array();
        if (logicalExpression == null) {
            return null;
        } else {
            var ruleNameRegExp = /[A-Z0-9_]+/mg;
            var ruleName = ruleNameRegExp.exec(logicalExpression);
            while (ruleName != null) {
                items.push(ruleName);
                ruleName = ruleNameRegExp.exec(logicalExpression);
            }
        }
        return items;
    }

    static getRule(ruleName: any, nosign: any) {
        if (ruleName) {
            for (var i = 0; i < nosign.length; i++) {
                if (nosign[i].name == ruleName) {
                    return nosign[i];
                }
            }
        }
    }

    static runMetaRule(rule: any, nosign: any, meta: any, body_raw: any, body_decoded: any, url: any, title: any, domain: any, scripts: any) {
        var ruleObjArr = new Array();
        var internal = null;
        var hitScripts = new Array();
        if (rule.rules != null) {
            var listOfRules = BDTLL.assl_common.extractRules(rule.rules);

            for (var i = 0; i < listOfRules.length; i++) {
                var ruleObj = {
                    rule: listOfRules[i],
                    hit: false
                }
                ruleObjArr.push(ruleObj);

            }
            for (var i = 0; i < ruleObjArr.length; i++) {

                if (ruleObjArr[i].rule.type == "metarule") {
                    var res1 = BDTLL.assl_common.runMetaRule(BDTLL.assl_common.getRule(ruleObjArr[i].rule, nosign), nosign, meta, body_raw, body_decoded, url, title, domain, scripts);
                    ruleObjArr[i].hit = res1.verdict;
                    hitScripts = hitScripts.concat(res1.scripts);
                } else {
                    var res2 = BDTLL.assl_common.runRule(BDTLL.assl_common.getRule(ruleObjArr[i].rule, nosign), meta, body_raw, body_decoded, url, title, domain, scripts);
                    ruleObjArr[i].hit = res2.verdict;
                    hitScripts = hitScripts.concat(res2.scripts);
                }
            }
            internal = rule.rules;

            if (internal) {
                for (var i = 0; i < ruleObjArr.length; i++) {
                    internal = internal.replace(ruleObjArr[i].rule, ruleObjArr[i].hit);
                }
                internal = internal.replace(/\&\&\&/g, "&&");

                internal = internal.replace(/\|\|\|/g, "||");
            }
            var evalexpr = BDTLL.Utils.evaluateExpression(internal);

            return {
            verdict: evalexpr,
            scripts: hitScripts
            };
        }
    }
}