import * as BDTLL from "../BDTLL";

export interface Iassl {
  signCreated: any;
  sign: any;
  nosign: any;
  trackSign: any;
  trackNoSign: any;
  createRegexes(): Promise<any>;
  scan(sign: any, nosign: any, meta: any, body_raw: any, body_decoded: any, url: any, title: any, domain: any, scripts: any): any;
}

export class assl implements Iassl {

  //----> Structures that hold the rules,metarules and metarules that have no action<------
  internalRule: any;

  signCreated = 0;
  sign: any;
  nosign: any;
  trackSign: any;
  trackNoSign: any;
  storage: BDTLL.IStorage;

  constructor(storage: BDTLL.IStorage) {
    this.storage = storage;
  }

  async createRegexes() {
    if (typeof(this.sign) == "undefined" || (typeof(this.sign) != "undefined" && this.sign.length == 0)) {
      this.sign = new Array();
      this.nosign = new Array();
      var slfContent = await this.storage.get(BDTLL.StorageKeys.SLF_CONTENT);

      var ruleParse = /^(rule|metarule)\s+(.*?)\s+{\s+condition:\s+(.*?);\s+actions:\s+(.*?);\s+metadata:\s+(.*?);/mg;
      var match = ruleParse.exec(slfContent);

      while (match != null) {
        var matchRegex = /match\("(.*)",\s*(.*?)\s*,\s*(.*?)\s*(,\s*(.*?))?\)|exec\((.*?)\)|(.*)/mgi;
        var actionRegex = /mark\("(.*?)"\s*,\s*(\d+)\)|(.*)/mgi;
        var metadataRegex = /priority\s*=\s*(\d+)|(.*)/mgi;

        var regExtract = matchRegex.exec(match[3]);
        var actionsExtract = actionRegex.exec(match[4]);
        var metadataExtract = metadataRegex.exec(match[5]);

        var currentRule = null;
        if (regExtract[1] != null) {
          var reg: any;
          try {
            if (regExtract[3] == "REGEX_INSENSITIVE") {
                reg = new RegExp(regExtract[1], "i");
            } else {
                reg = new RegExp(regExtract[1]);
            }
          } catch (err) {
            reg = "regex fail";
          }

          currentRule = {
            type: match[1],
            name: match[2],
            regex: reg,
            part: regExtract[2],
            param: regExtract[3],
            encoding: regExtract[5],
            action: actionsExtract[1],
            score: actionsExtract[2],
            priority: metadataExtract[1]
          }
        } else if (regExtract[6] != null) {
          currentRule = {
            type: match[1],
            name: match[2],
            exec: regExtract[6],
            action: actionsExtract[1],
            score: actionsExtract[2],
            priority: metadataExtract[1]
          }
        } else if (regExtract[7] != null) {
          currentRule = {
            type: match[1],
            name: match[2],
            rules: regExtract[7],
            action: actionsExtract[1],
            score: actionsExtract[2],
            priority: metadataExtract[1]
          }
        }
        if (currentRule.action != null) {
          this.sign.push(currentRule);
        } else {
          this.nosign.push(currentRule);
        }

        match = ruleParse.exec(slfContent);
      }

      this.sign.sort((a: any, b: any) => {
        return b.priority - a.priority
      });

    }
    return 1;
  }

  scan(sign: any, nosign: any, meta: any, body_raw: any, body_decoded: any, url: any, title: any, domain: any, scripts: any) {
    var answer = new Array();

    var currentTime = new Date();
    var hitScripts = new Array();
    var name = "";
    for (var i = 0; i < sign.length; i++)

    {

      if ((sign[i].type == "rule") && (sign[i].action != null)) {
        var result = BDTLL.assl_common.runRule(sign[i], meta, body_raw, body_decoded, url, title, domain, scripts)
        if (result.verdict == true) {
          if (sign[i].action == "PHISHING" || sign[i].action == "LEGIT") {
            name = sign[i].name;
            return {
              action: sign[i].action,
              name: name
            };
          }
        }
      } else if ((sign[i].type == "metarule") && (sign[i].action != null))

      {
        try {
          var runmeta = BDTLL.assl_common.runMetaRule(sign[i], nosign, meta, body_raw, body_decoded, url, title, domain, scripts);

          if (runmeta.verdict == true) {
            if (sign[i].action == "PHISHING" || sign[i].action == "LEGIT") {
              name = sign[i].name;
              return {
                action: sign[i].action,
                name: name
              };
            }
          }
        } catch (err) {}
      }
    }
    var currentTime = new Date();
      return {
        action: "IGNORE",
        name: "IGNORE"
      };
  }

}