import * as BDTLL from "../BDTLL";
let browser = require("webextension-polyfill");

export class Update {
	storage: BDTLL.IStorage;
	scanner: BDTLL.IScanner;
	assl: BDTLL.Iassl;
	interval = 14400000; // 4 hours between updates
	intervalInMInutes = 240;  // 4 hours between updates (in minutes)

	constructor(storage: BDTLL.IStorage, scanner: BDTLL.IScanner, assl: BDTLL.Iassl) {
		this.storage = storage;
		this.scanner = scanner;
		this.assl = assl;
		this.checkUpdate();

		browser.alarms.get("tll-update-timer").then((alarm: any) => {
			if (typeof(alarm) === "undefined") {
				// Move timers to alarms in service workers:
				// https://developer.chrome.com/docs/extensions/mv3/migrating_to_service_workers/#alarms
				browser.alarms.create(
					"tll-update-timer",
					{
						// length of time after which the `onAlarm` event is fired for the first time.
						delayInMinutes: this.intervalInMInutes,
						// the `onAlarm` event should fire every `periodInMinutes` minutes after first initial event.
						periodInMinutes: this.intervalInMInutes,
					}
				)
			} else {
				if (BDTLL.DEBUG_MODE) {
					console.log("tll-update-timer already registered!");
				}
			}
		});

		this.listen();
	}

	async checkUpdate() {
		try {
			var currentTime = (new Date()).getTime();
			var storedTime = parseInt(await this.storage.get(BDTLL.StorageKeys.TIME));

			if (!(isNaN(storedTime) || storedTime + this.interval < currentTime))
				return 0;

			this.storage.set(BDTLL.StorageKeys.TIME, currentTime);
			this.update().catch((reason: any) => {
				// can't update: check reason
			});
		} catch (err) { }
	}

	async update() {
		var date = new Date();
		this.storage.set(BDTLL.StorageKeys.DATE, date.toLocaleDateString());

		this.requestInfo("ph_sign.slf")
			.then((response: any) => response.text())
			.then(async (body: any) => {
				this.assl.sign = new Array();
				this.assl.nosign = new Array();
				this.storage.set(BDTLL.StorageKeys.SLF_CONTENT, body);
				var version = await this.assl.createRegexes();
			})
			.catch((err) => {
				if (BDTLL.DEBUG_MODE) {
					console.log(err);
				}
			});

		this.requestInfo("ph_white.txt")
			.then((response: any) => response.text())
			.then((body: any) => {    
				this.storage.set(BDTLL.StorageKeys.INTERNAL_WHITELIST, body).then(() => {
					this.scanner.internalWhitelist = body.split("\n");
				})
				.catch((err) => {
					if (BDTLL.DEBUG_MODE) {
						console.log(err);
					}
				});
			})
			.catch((err) => {
				if (BDTLL.DEBUG_MODE) {
					console.log(err);
				}
			});
	}

	requestInfo(filename: string) {
		const fetchPromise = fetch(
			`https://nimbus.bitdefender.net/tll/update?file=${filename}`,
			{
				method: 'GET',
				headers: {
					'X-Nimbus-ClientId': BDTLL.BrowserConsts.BROWSER_CLIENTID || '',
				}
			}
		);

		return fetchPromise;
	}

	listen() {
		BDTLL.MessageService.addListener(
			(message: any, sender: any) => {
				if (message.command == BDTLL.Command.GET_LAST_UPDATE)
					return this.storage.get(BDTLL.StorageKeys.DATE);
			}
		);

		browser.alarms.onAlarm.addListener((alarm: any) => {
			if (alarm.name === "tll-update-timer") {
				if (BDTLL.DEBUG_MODE) {
					console.log("Check updates...");
				}
				this.checkUpdate();
			}
		});
	}
}