import * as BDTLL from "../BDTLL_common";

let browser = require("webextension-polyfill");

export interface IMessage {
    command?: BDTLL.Command,
    type?: BDTLL.SettingType,
    value?: any,
}

export class  MessageService{
    public static async messageBackground(message: any): Promise<any> {
        return browser.runtime.sendMessage(message);
    }

    public static async messageForeground(tabId: string, message: any): Promise<any> {
        return browser.tabs.sendMessage(tabId, message).catch((e: any) => {
            // known error
            if (e.message == "The message port closed before a response was received.")
                return;

            throw e;
        });
    }

    public static addListener(callback: any) {
        browser.runtime.onMessage.addListener(callback);
    }

    public static removeListener(listener: any) {
        browser.runtime.onMessage.removeListener(listener);
    }

    public static hasListener(listener: any) {
        browser.runtime.onMessage.hasListener(listener);
    }

}