import * as BDTLL from "../BDTLL";

export interface IScanner {
    internalWhitelist: Array<string>;
    getNumberOfScannedPages(): number;
    resetScannedPagesCounter(): void;
    scanLink(url: string): Promise<BDTLL.PageStatus>,
    scanPage(request: any): Promise<BDTLL.PageStatus>,
}

export class Scanner implements IScanner {
    internalWhitelist: Array<string>;
    scannedPagesCounter: number;
    storage: BDTLL.IStorage;
    assl: BDTLL.Iassl;

    constructor(storage: BDTLL.IStorage, assl: BDTLL.Iassl) {
        this.storage = storage;
        storage.get(BDTLL.StorageKeys.INTERNAL_WHITELIST).then((response: string) => {
            response = response || "";
            this.internalWhitelist = response.split("");
        });
        storage.get(BDTLL.StorageKeys.TLL_SP).then((counter: number) => {
            this.scannedPagesCounter = counter || 0;
        });
        this.assl = assl;
        this.listen();
    }

    async scanLink(url: string): Promise<BDTLL.PageStatus> {
        if (this.internalWhitelist !== undefined && this.internalWhitelist.indexOf(url) > -1)
            return BDTLL.PageStatus.SAFE;

        var status = await BDTLL.CloudTalk.interogateCloud(url);
        this.scannedPagesCounter += 1;
        this.storage.set(BDTLL.StorageKeys.TLL_SP, this.scannedPagesCounter).then(() => {});

        return (status as BDTLL.PageStatus);
    }

    async scanLinks(urls: string[]): Promise<Array<BDTLL.PageStatus>> {
        var statuses = BDTLL.CloudTalk.interogateCloud(urls)
            .then((statuses: Array<BDTLL.PageStatus>) => {
                return statuses as BDTLL.PageStatus[]
            });
        this.scannedPagesCounter += urls.length;
        this.storage.set(BDTLL.StorageKeys.TLL_SP, this.scannedPagesCounter).then(() => {});

        return statuses;
    }

    async scanPage(request: any): Promise<BDTLL.PageStatus> {
        var verdict = BDTLL.PageStatus.SAFE;

        if (this.internalWhitelist !== undefined && this.internalWhitelist.indexOf(request.url) > -1)
            return verdict;

        var decoded_body = BDTLL.Utils.removeHTMLTags(request.body);
        var version = await this.assl.createRegexes();

        var resp = this.assl.scan(this.assl.sign,
          this.assl.nosign,
          request.meta,
          request.body,
          decoded_body,
          request.url,
          request.title,
          request.domain,
          request.scripts);

        if (resp.action == "PHISHING") {
            verdict = BDTLL.PageStatus.PHISHING;
            BDTLL.Utils.reportCatch(request.url, "TLL", BDTLL.Consts.VERSION + "_chrome_" + await this.storage.get(BDTLL.StorageKeys.DATE), resp.name);
        }

        return verdict;
    }

    getNumberOfScannedPages(): number {
        return this.scannedPagesCounter;
    }

    resetScannedPagesCounter(): void {
        this.storage.set(BDTLL.StorageKeys.TLL_SP, 0).then(() => {
            if (BDTLL.DEBUG_MODE) {
                console.log("Counter was reset!");
            }
            this.scannedPagesCounter = 0;
        }).catch((err: any) => {
            if (BDTLL.DEBUG_MODE) {
                console.error(err);
            }
        });
    }

    listen() {
        BDTLL.MessageService.addListener(
            (message: any, sender: any) => {
                if (message.command == BDTLL.Command.SCAN_LINKS) {
                    return this.scanLinks(message.urls);
                }
            }
        )
    }
}