import * as BDTLL from "../BDTLL";

let browser = require("webextension-polyfill");

export interface ISwitchable {
    enable(setting: BDTLL.SettingType): void;
    disable(setting: BDTLL.SettingType): void;
}

export interface ISettings {
    storage: BDTLL.IStorage;
    get(setting: BDTLL.SettingType): Promise<boolean>;
    set(setting: BDTLL.SettingType, value: boolean): Promise<boolean>;
    listen(): void;
}

export class Settings implements ISettings {
    storage: BDTLL.IStorage;
    modules: Array<BDTLL.ISwitchable>;

    constructor(storage: BDTLL.IStorage, modules: Array<BDTLL.ISwitchable>) {
        this.storage = storage;
        this.modules = modules;
        this.init(BDTLL.SettingType.THREAT_FILTER);
        this.init(BDTLL.SettingType.SEARCH_ANALYZER);
        this.listen();
    }

    async init(setting: BDTLL.SettingType) {
        this.storage.get(setting).then((value: any) => {
            var val = (value == null? true: (value === "true" || value === true));
            this.set(setting, val);
        });
    }

    async get(setting: BDTLL.SettingType) {
        var value = await this.storage.get(setting);

        return (value === "true" || value === true);
    }

    async set(setting: BDTLL.SettingType, value: boolean) {
        for (let module of this.modules)
            value ? module.enable(setting) : module.disable(setting);

        return this.storage.set(setting, String(value));
    }

    listen() {
        BDTLL.MessageService.addListener(
            (message: any, sender: any) => {
                if (message.command == BDTLL.Command.GET_SETTING) {
                    return this.get(message.type)
                } else if(message.command == BDTLL.Command.SET_SETTING) {
                    return this.set(message.type, message.value);
                }
            }
        );
    }
}