import * as BDTLL from "../BDTLL";

export class CloudTalk {
	static extractVerdict(item: any) {
		var index = BDTLL.StatusPriority.indexOf(BDTLL.PageStatus.SAFE);

		if (item.status_message instanceof Array) {
			index = Math.max(index, ...item.status_message.map((status: string) => {
				return BDTLL.StatusPriority.indexOf(status as BDTLL.PageStatus);
			}));
		}
		else
			index = Math.max(BDTLL.StatusPriority.indexOf(item.status_message as BDTLL.PageStatus), index);

		return BDTLL.StatusPriority[index];
	}

	static async interogateCloud(url: string | string[]) {
		let headers: { [key: string]: string } = {
			'Content-Type': 'application/json',
			'X-Nimbus-ClientId': BDTLL.BrowserConsts.BROWSER_CLIENTID || '',
		};

		let requestData;
		let endpoint = BDTLL.URLStatusEndpoints.URL_STATUS;
		if (url instanceof Array) {
			endpoint = BDTLL.URLStatusEndpoints.URL_BATCH_STATUS;
			requestData = url.map(item => { return { url: item }});
		} else {
			requestData = {
				url: url
			};
		}

		const fetchResponse = await fetch(`${BDTLL.CLOUD_SERVER}/${endpoint}`, {
			method: "POST",
			headers: headers,
			body: JSON.stringify(requestData)
		}).then((response: any) => {
			return response.json();
		}).then((response: any) => {
			if (response instanceof Array) {
				return response.map(item => this.extractVerdict(item));
			} else {
				return this.extractVerdict(response);
			}
		}).catch((error: any) => {
			if (BDTLL.DEBUG_MODE) {
				console.error(error);
			}
		});

		return fetchResponse;
  	}
}